





















































import {Component, Vue} from "vue-property-decorator";

@Component
export default class RealTimeData extends Vue {

    timer: any;

    data: {
        [key: string]: any,
        algorithm: string,
        nethash: string,
        poolhash: string,
        workers: string,
        fee: string,
        period: string
    } = {
        "algorithm": "",
        "nethash": "",
        "poolhash": "",
        "workers": "",
        "fee": "",
        "period": ""
    };

    mounted() {

        this.timer = setInterval(() => {
            this.loadData().then();
        }, 1000);
    }

    beforeDestroy() {
        clearInterval(this.timer);
    }

    async loadData() {
        const result = await fetch("./test.json").then(function (response) {
            return response.json();
        });
        for (const k in this.data) {
            if (result[k]) {
                this.data[k] = result[k];
            }
        }
    }
}
